import { registerUser } from "../../../services/auth";
import router from "../../../router";

export default {
    name: "register",
    data() {
        return {
            companyCode: '',
            email: '',
            password: '',
            errorMsg: '',
            successMsg: '',
            mobile: '',
            first_name: '',
            last_name: ''
        };
    },
    methods: {
        onRegisterSubmit(event) {
            this.errorMsg = '';
            this.successMsg = '';
            // console.log(this.companyCode)
            var params = {
				first_name: this.first_name,
				last_name: this.last_name,
                email: this.email,
                password: this.password,
                mobile: this.mobile,
                companyCode: this.companyCode
            };
            
            event.preventDefault(); 
            registerUser(params).then((result) =>{
                if(result.data.success){
                    this.$store.dispatch("setLoginStatus", true);
                    this.$store.dispatch("setUserInfo",{
                        "first_name": result.data.userInfo.first_name,
                        "last_name": result.data.userInfo.last_name,
                        "companyCode": this.companyCode,
                        // "userId": result.data.userInfo.userId
                    });
                    
                    this.successMsg = "Registered! Contact connect@growlytics.in to get started with " + this.companyCode + " projects setup."

                }else{
                    //Show invalid error message
                    this.errorMsg = result.data.message;
                }
            }).catch((error) =>{
                //Internal error
                console.log(error)
                this.errorMsg = "Internal Error.";
            });
        }
    }
};


